.a1:after {
    content: "";
    position: absolute;
    /* pointer-events: none; */
    /* position: fixed; */
    background-color: white;
    height: 3px;
    width: 0%;
    transform: translateX(-50%);
    left: 71%;
    bottom: 0;
    transition: .35s ease;
}

.a2:after {
    content: "";
    position: absolute;
    background-color: white;
    height: 3px;
    width: 0%;
    transform: translateX(-50%);
    left: 71%;
    bottom: 0;
    transition: .35s ease;
}

.a3:after,
.a4:after,
.a5:after {
    content: "";
    position: absolute;
    background-color: white;
    height: 3px;
    width: 0%;
    transform: translateX(-50%);
    left: 71%;
    bottom: 0;
    transition: .35s ease;
}

.a1:not(.nohover):after {
    width: 100%;

}

.a2:hover:after {
    width: 100%;
}

.a3:hover:after {
    width: 100%;
}

